<template>
  <el-container class="container">
    <el-header>
      <div class="logo">
        <router-link tag="div" to="/">
          <i class="el-icon-s-custom"></i>
          <span>银行家算法</span>
        </router-link>
      </div>
      <div class="btns">
        <el-button
          type="success"
          v-if="!isStarted"
          icon="el-icon-video-play"
          @click="start"
          >启动</el-button
        >
        <el-button type="success" v-else icon="el-icon-refresh" @click="start"
          >重新启动</el-button
        >
        <el-button 
          type="info"
          icon="el-icon-money"
          @click="dialogFormVisible = true"
          :disabled="!isStarted"
          >贷款</el-button
        >
        <el-button
          type="primary"
          icon="el-icon-search"
          @click="dialogTableVisible = true"
          :disabled="!isStarted"
          >查看安全性算法</el-button
        >
      </div>
    </el-header>
    <el-main>
      <div v-if="!isStarted">
        <a-empty />
      </div>
      <div v-else class="body">
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <h3>资源分配详情</h3>
            <h3>
              资源剩余
              <span v-for="(item, index) in bankers.available" :key="index">
                {{ String.fromCharCode(65 + index) + ":" + item }}
              </span>
            </h3>
          </div>
          <el-table :data="adapterBankers" stripe style="width: 100%">
            <el-table-column prop="name" label="进程" width="180">
            </el-table-column>
            <el-table-column prop="max" label="Max" width="180">
              <el-table-column
                v-for="i in num"
                :key="i"
                :prop="'max.' + String.fromCharCode(64 + i)"
                :label="String.fromCharCode(64 + i)"
              >
              </el-table-column>
            </el-table-column>
            <el-table-column prop="allocation" label="Allocation" width="180">
              <el-table-column
                v-for="i in num"
                :key="i"
                :prop="'allocation.' + String.fromCharCode(64 + i)"
                :label="String.fromCharCode(64 + i)"
              >
              </el-table-column>
            </el-table-column>
            <el-table-column prop="need" label="Need" width="180">
              <el-table-column
                v-for="i in num"
                :key="i"
                :prop="'need.' + String.fromCharCode(64 + i)"
                :label="String.fromCharCode(64 + i)"
              >
              </el-table-column>
            </el-table-column>
          </el-table>
        </el-card>
      </div>
    </el-main>
    <!-- 申请贷款 -->
    <el-dialog
      title="贷款"
      :visible.sync="dialogFormVisible"
      @open="openFormDialog"
      width="500px"
    >
      <el-form :model="form">
        <el-form-item label="进程" :label-width="formLabelWidth">
          <el-select v-model="form.id" placeholder="请选择">
            <el-option
              v-for="item in name"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item
          :label-width="formLabelWidth"
          v-for="i in num"
          :key="i"
          :label="'资源' + String.fromCharCode(64 + i)"
        >
          <el-input
            v-model="form.request[i - 1]"
            placeholder="请输入内容"
          ></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="loan">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 查看安全性算法 -->
    <el-dialog
      title="安全性算法 "
      :visible.sync="dialogTableVisible"
      @open="openTitleDialog"
      width="80%"
    >
      <el-table :data="adapterSafety" stripe style="width: 100%">
        <el-table-column prop="name" label="进程"> </el-table-column>
        <el-table-column prop="work" label="Work">
          <el-table-column
            v-for="i in num"
            :key="i"
            :prop="'work.' + String.fromCharCode(64 + i)"
            :label="String.fromCharCode(64 + i)"
          >
          </el-table-column>
        </el-table-column>
        <el-table-column prop="allocation" label="Allocation">
          <el-table-column
            v-for="i in num"
            :key="i"
            :prop="'allocation.' + String.fromCharCode(64 + i)"
            :label="String.fromCharCode(64 + i)"
          >
          </el-table-column>
        </el-table-column>
        <el-table-column prop="need" label="Need">
          <el-table-column
            v-for="i in num"
            :key="i"
            :prop="'need.' + String.fromCharCode(64 + i)"
            :label="String.fromCharCode(64 + i)"
          >
          </el-table-column>
        </el-table-column>
        <el-table-column prop="workAndAllocation" label="Work+Allocation">
          <el-table-column
            v-for="i in num"
            :key="i"
            :prop="'workAndAllocation.' + String.fromCharCode(64 + i)"
            :label="String.fromCharCode(64 + i)"
          >
          </el-table-column>
        </el-table-column>
        <el-table-column prop="finish" label="Finish">
          <template slot-scope="scope">
            {{ scope.row.finish }}
          </template>
        </el-table-column>
      </el-table>
    </el-dialog>
  </el-container>
</template>

<script>
export default {
  data() {
    return {
      isStarted: false,
      num: 0,
      bankers: null,
      adapterBankers: [],
      safety: null,
      adapterSafety: [],
      dialogFormVisible: false,
      dialogTableVisible: false,
      form: {
        id: null,
        request: [],
      },
      name: [],
      formLabelWidth: "100px",
    };
  },
  watch: {
    bankers(newVal) {
      this.adapterBankers = [];
      for (let i = 0; i < newVal.max.length; i++) {
        this.num = newVal.max[0].length;
        let element = {
          name: newVal.name[i],
          max: {},
          allocation: {},
          need: {},
        };
        for (let j = 0; j < newVal.max[i].length; j++) {
          let ch = String.fromCharCode(65 + j);
          element.max[ch] = newVal.max[i][j];
          element.allocation[ch] = newVal.allocation[i][j];
          element.need[ch] = newVal.need[i][j];
        }
        this.adapterBankers.push(element);
      }
    },
    safety(newVal) {
      this.adapterSafety = [];
      if (newVal == null) {
        return;
      }
      for (let i = 0; i < newVal.length; i++) {
        let element = {
          name: newVal[i].name,
          work: {},
          allocation: {},
          need: {},
          workAndAllocation: {},
          finish: newVal[i].finish,
        };
        for (let j = 0; j < this.num; j++) {
          let ch = String.fromCharCode(65 + j);
          element.work[ch] = newVal[i].work[j];
          element.allocation[ch] = newVal[i].allocation[j];
          element.need[ch] = newVal[i].need[j];
          element.workAndAllocation[ch] = newVal[i].workAndAllocation[j];
        }
        this.adapterSafety.push(element);
      }
    },
  },
  created() {
    this.info();
  },
  methods: {
    async start() {
      const { data: res } = await this.$http.post("start");
      if (res.code == 100) {
        this.isStarted = true;
        this.bankers = res.data.bankers;
        if (res.data.bankers.safety != null) {
          this.safety = res.data.bankers.safety.recordList;
        } else {
          this.safety = [];
        }
      }
    },
    async info() {
      const { data: res } = await this.$http.get("info");
      if (res.code == 100) {
        this.isStarted = true;
        this.bankers = res.data.bankers;
        if (res.data.bankers.safety != null) {
          this.safety = res.data.bankers.safety.recordList;
        } else {
          this.safety = [];
        }
      }
    },
    async loan() {
      this.dialogFormVisible = false;
      const { data: res } = await this.$http.post(
        "loan/" + this.form.id,
        this.form.request
      );
      if (res.code == 100) {
        this.isStarted = true;
        this.bankers = res.data.bankers;
        this.safety = res.data.bankers.safety.recordList;
        this.$message({
          showClose: true,
          message: res.msg,
          type: "success",
          offset: 70,
        });
      } else {
        this.safety = null;
        this.$message({
          showClose: true,
          message: res.msg,
          type: "error",
          offset: 70,
        });
      }
    },
    openFormDialog() {
      if (this.bankers == null) {
        return;
      }
      this.name = [];
      this.form.request = [];
      for (let i = 0; i < this.bankers.name.length; i++) {
        const element = {
          label: this.bankers.name[i],
          value: i,
        };
        this.name.push(element);
      }
    },
    openTitleDialog() {},
  },
};
</script>

<style lang="scss">
.container {
  .el-header {
    .logo {
      i {
        margin-right: 10px;
      }
      display: flex;
      align-items: center;
      font-size: 20px;
    }
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 15%;
    border-bottom: 1px solid #eee;
  }
  .body {
    margin: 0 10%;
    .clearfix{
      padding: 0 5%;
      >h3:nth-child(2) {
        float: right;
      }
      >h3:nth-child(1) {
        float: left;
      }
      overflow: hidden;
    }
  }
}
</style>